import { Component, OnInit } from '@angular/core';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { LoadingController,AlertController,ModalController,NavController, Platform } from '@ionic/angular';
import { EmailComposer } from '@ionic-native/email-composer/ngx';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import {ProfileService} from '../../services/profile/profile.service';
import * as moment from 'moment';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.page.html',
  styleUrls: ['./settings.page.scss'],
})
export class SettingsPage implements OnInit {

    public user:any;
    public paymentTotal:number;
    
    constructor(private emailComposer: EmailComposer,public plt: Platform, private profileService: ProfileService,
        private authenticationService: AuthenticationService, private alertCtrl: AlertController, private iab: InAppBrowser) {
        
        this.user = {};
        this.authenticationService.getUserData().subscribe((value) => {
            this.user = value;
            
        });
        
        this.paymentTotal = 0;
        
        
    }
    
    ngOnInit() {
    }  
    
    ionViewDidEnter(){
        this.getPaymentTotal();
    }
    
    
    public getBankAccount(){
        if (!this.user.profile || !this.user.profile.bank_account){
            return "No bank account set";
        }
        else{
            return this.user.profile.bank_account.replace(/(\+?\d{3})(\d+)(\d{2})/g, function(match, start, middle, end) {
              return start + "*".repeat(middle.length) + end;
            });
        }
    }
    
    
    public getPaymentTotal(){
        this.authenticationService.getPaymentTotal().then((data:number) => {
            this.paymentTotal = data;
            
        })
    }    
    
    public getNextPaymentDate(){
        return moment().add(1,'months').startOf('month').format("DD/MM/YYYY");
    }
    
    public openRate(){
        
        if (this.plt.is("ios")){
            this.iab.create("https://apps.apple.com/app/", '_system');
        }
        else{
            this.iab.create("https://play.google.com/store/apps/details?id=com.musicbuddy.app", '_system');
        }
        
        
    }
    public openFollow(){
        let link = 'https://www.facebook.com/musicbuddy';
        this.iab.create(link, '_system');        
    }
 
    
    public openEmail(){
        let email = {
          to: ["musicbuddynz@gmail.com"],
          subject: 'Subject for your support query',
          body: 'Provide details about the support you require.',
          isHtml: true
        };        

        this.emailComposer.open(email);
    }
    
    
    public openChangeBankAccount(){
        let alert = this.alertCtrl.create({
            header: 'Change Bank Account',
            inputs: [           
                {
                    name: 'bank_account',
                    placeholder: 'Bank Account Number',
                    type: 'number'
                }
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                    }
                },
                {
                    text: 'Change',
                    handler: data => {
                        
                        if (data.bank_account){
                            let profile = {bank_account:data.bank_account, user_id:this.user.id};
                            this.user.profile.bank_account = data.bank_account;
                            this.profileService.updateProfile(profile).then(() => {

                            })                              
                        }

    
                    }
                }      
            ]
        }).then((el) => {
            el.present();
        });          
    }    

    
    public openChangePassword(){
        let alert = this.alertCtrl.create({
            header: 'Change Password',
            inputs: [           
                {
                    name: 'password',
                    placeholder: 'Password',
                    type: 'password'
                },
                {
                    name: 'confirmPassword',
                    placeholder: 'Confirm Password',
                    type: 'password'
                }
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                    }
                },
                {
                    text: 'Change',
                    handler: data => {
                        
                        if (data.password && data.confirmPassword && data.password === data.confirmPassword){

                            
                            this.authenticationService.changePassword(this.user.email, data.password, data.confirmPassword).then(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Success',
                                    message: 'Your password has been updated',
                                    buttons: [
                                        {
                                            text: 'Dismiss',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        }                              
                                    ]           
                                }).then((el) => {
                                    el.present();
                                });                                 
                            }).catch(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Error',
                                    message: 'There was an error while changing your password.',
                                    buttons: [
                                        {
                                            text: 'Cancel',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        },
                                         {
                                            text: 'Try Again',
                                            handler: data => {
                                                setTimeout(() => {this.openChangePassword()},200);
                                            }
                                        }                               
                                    ]            
                                }).then((el) => {
                                    el.present();
                                });                                
                            });
                        }
                        
                        else{
                            let alert = this.alertCtrl.create({
                                header: 'Error',
                                message: 'Passwords do not match',
                                buttons: [
                                    {
                                        text: 'Cancel',
                                        role: 'cancel',
                                        handler: data => {
                                        }
                                    },
                                     {
                                        text: 'Try Again',
                                        handler: data => {
                                            setTimeout(() => {this.openChangePassword()},200);
                                        }
                                    }                               
                                ]            
                            }).then((el) => {
                                    el.present();
                                });                              
                        }
    
                    }
                }
                
            ]
        }).then((el) => {
            el.present();
        });          
    }
    
    
    public changeView(ev){

    }
    
    
    public logout(){
        this.authenticationService.logout();
      
    }      
}